@import url('https://fonts.googleapis.com/css2?family=Lexend+Giga&display=swap');

.App {
  text-align: center;
  background-color: #F9F7EB;
  height: 812px;
  width: 100%;
  font-family: 'Lexend Giga', sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

 .mobile-container {
  max-width: 480px;
  margin: auto;
  background-color: #555;
  height: 500px;
  color: white;
  border-radius: 10px;
}

.topNav {
  overflow: hidden;
    /* background-color: #A9B4A2; */
    position: relative;
    list-style-type: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    padding: 10px;
}

.topNav ul {
  list-style: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.topNav a {
  text-decoration: none;
  color: white;
}

.nav-link-color {
  background-color: #A9B4A2;
  padding: 4px 1px;
}

.topnav #myLinks {
  display: none;
}

.topnav a {
  color: white;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  display: block;
}

.topnav a.icon {
  background: black;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
}

.topnav a:hover {
  background-color: #ddd;
  color: black;
}

.active {
  background-color: #4CAF50;
} 


.day-toggle {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.next-day {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.lottie-container{
  transform: scale(0.5);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.plans {
  align-content: center;
    background-color: #A9B4A2;
    /* box-shadow: 8px 8px 0 rgba(251,59,73,.2); */
    color: white;
    font-size: 1.3em;
    /* letter-spacing: 0.5px; */
    text-align: center;
    text-decoration: none;
    padding: 16px;
    border-radius: 60px;
    width: 14%;
    /* margin: 0; */
    border-style: none;
    padding: 16px 27px;
}

.plus-button {
  display: flex;
    /* align-items: center; */
    /* align-content: center; */
    /* margin-right: 0px; */
    right: 10px;
    transform: scale(1.3);
    position: relative;
}

.nav-btn {
  position: fixed;
  top: 55px;
  float: right;
  right: 12px;
  margin-right: 19px;
}


.nav-btm {
  display: flex;
  flex-direction: row;
}

.btm-links {
  display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    align-content: center;
    background-color: #122F24;
    bottom: -16px;
    padding: 10px 0px;
    position: fixed;
    margin: 15px 0px;
    width: 100%;
}

.btm-links ul {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.bottom-link {
  padding: 10px;
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.home-btn {
  padding: 10px 17px;
    list-style: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 6px;
}

.enter {
  display: flex;
  flex-direction: row;
  align-items: center;
  align-content: center;
  padding: 10px;
  position: absolute;
  width: 180px;
  height: 42px;
  left: 95px;
  top: 632px;
  background: #122F24;
  border-radius: 30px;
}

.enter-link  {
  text-decoration: none;
  color: white;
  
}